<template>
	<div class="list">
      <div class="list-item-top">
        <div class="list-item-top-line">
          <div class="item-left">
            <strong class="item-tit">退款进度</strong>
          </div>
          <div class="item-right">
            <span class="msg-default msg-primay">{{curstatus == 6?'退款审核中...':'已退款'}}</span>
          </div>
        </div>
        <div class="list-item-top-line">
          <div class="item-left">
            <strong class="item-tit"></strong>
          </div>
          <div class="item-right">
            <span class="msg-time">2018-08-20 12:30</span>
          </div>
        </div>
      </div>
      <div class="list-item">
        <div class="item-left">
          <strong class="item-tit">退款信息</strong>
        </div>
        <div class="item-right">
          <span class="msg-default">{{curCertName || '无'}}</span>
        </div>
      </div>
      <div class="list-item">
        <div class="item-left">
          <strong class="item-tit">退款金额</strong>
        </div>
        <div class="item-right">
          <span class="msg-default msg-primay">300元</span>
        </div>
      </div>
      <div class="list-item">
        <div class="item-left">
          <strong class="item-tit">申请时间</strong>
        </div>
        <div class="item-right">
          <span class="msg-default">2018-09-21 11:34:57</span>
        </div>
      </div>
      <div class="list-item">
        <div class="item-left">
          <strong class="item-tit">退款编号</strong>
        </div>
        <div class="item-right">
          <span class="msg-default">9887455000014</span>
        </div>
      </div>
    </div>

</template>

<script>
export default {
  name: 'Refund',
  data () {
      return {
        openid:'',
        curCertId:'',
        curstatus:'',
        curCertName:''



      }
  },
  created:function(){
    this.getParams();
  },
  methods: {
    getParams(){
      var self = this;
      self.curCertId = self.$route.query.curcertid;
      self.openid = localStorage.openid;
      self.curCertName = localStorage.cert_name;
      self.curstatus = localStorage.cert_status;
      self.getOrderData();
    },
    getOrderData(){
      let self = this;
      self.$axios.get(self.baseUrl+'/api/v1/weixin/wxquery/'+self.openid + '/'+self.curCertId).then(res =>{
        if(res.data.success){
          console.log(res.data.data);
        }else{
          self.$toast.center(res.data.msg);
        }

      })


    },




   },
   computed: {

   }




}
</script>
<style scoped>
.list{
    width:100%;
    margin-top:20px;
    display: -webkit-flex;
    display: flex;
    padding:40px 30px;
    flex-direction: column;
    background-color:#fff;
    border-top:1px solid #f3f3f5;/*no*/
}
.list-item{
    width:100%;
    height:90px;
    line-height:90px;
    padding:0;
    display: -webkit-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
}
.list-item-top{
  height:148px;
  padding:0;
  border-bottom:1px solid #ddd;/*no*/
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  margin-bottom:20px;
}
.list-item-top-line{
  width:100%;
  height:60px;
  line-height:60px;
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
}
.msg-time{
  font-size:26px;
  color:#999;
  padding-right:10px;
  letter-spacing: 1.97px;
}
.item-left{

}
.item-right{

}
.item-tit{
    font-size:28px;
    color:#666;
    font-weight: normal;
    padding-left:10px;
}
.msg-default{
    font-size:28px;
    padding-right:10px;
    letter-spacing: 1.97px;
}
.msg-err{
    color:#e83535;
}
.msg-primay{
    color:#d19421;
}
.img-cont{
    width:86px;
    height:86px;
    vertical-align: middle;
}
.img-icon{
    width:12px;
    height:21px;
    /*vertical-align: middle;*/

}
img{
    width:100%;
    height:100%;
}
</style>



